@import "sass/index";

.root {
  display: flex;
  flex-direction: row;

  @media (max-width: 800px) {
    flex-direction: column;
    align-items: center;
  }
}
.info {
  padding: 0 20px;
  width: 340px;
  & > :first-child {
    margin-bottom: 20px;
  }
}
.raceImg {
  height: 33px;
  width: 44px;
}
.raceTitle {
  font-size: 25px;
  line-height: 30px;
  font-weight: bold;
  letter-spacing: -1.15px;
}
.raceText {
  opacity: 0.75;
  font-size: 13px;
  line-height: 19px;
  font-weight: 500;
}
.raceBox {
  background: #b3f6aa;
  border-radius: 14px;
  padding: 15px;
  display: flex;
  flex-direction: column;
  color: #2f2b2e;
}
.tableWrapper {
  width: 100%;
  overflow-x: auto;
}

.tCell {
  border-bottom-color: use(--template-chat-massage-author-color, 0.1);
  padding: 20px;
  font-weight: 600;
  @media (max-width: 600px) {
    padding: 17px;
  }

  & > p {
    white-space: nowrap;
  }
}
.tableTitle {
  margin-left: 20px;
  font-weight: bold;
  color: use(--template-chat-massage-textBlock-color);
  font-size: 25px;
  line-height: 30px;
  letter-spacing: -1.15px;
}

.tCellData {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.circle {
  display: flex;
  background: use(--template-statisticsBlock-tab-avatarBackground);
  border-radius: 50%;
  margin-right: spacing(1.75);
}
.userLogo {
  width: 32px;
  height: 32px;
}

.medal {
  height: 30px;
}

.timeBox {
  display: flex;
  flex-direction: column;
  background: use(--template-chat-massage-author-color, 0.1);
  padding: 13px;
  border-radius: 8px;
  margin-bottom: 17px;

  color: use(--template-chat-massage-author-color);
}
.timeTitle {
  text-transform: uppercase;
  font-size: 12px;
  line-height: 15px;
  letter-spacing: 0.86px;
  color: use(--template-chat-massage-author-color);
  font-weight: bold;
  margin: 0;
  margin-bottom: 5px;
}
.timeText {
  margin: 0;
  line-height: 100%;
}
.wageredAmount {
  color: use(--template-chat-massage-textBlock-color);
  font-size: 23px;
  font-weight: bold;
  line-height: 28px;
  letter-spacing: 1.64px;
  & > span {
    font-size: 12px;
    line-height: 15px;
    letter-spacing: 0.86px;
    opacity: 0.4;
    margin: 0 5px;
  }
}
.timer {
  display: flex;
  & > div {
    color: use(--template-chat-massage-textBlock-color);
    font-size: 23px;
    font-weight: bold;
    line-height: 28px;
    letter-spacing: 1.65px;
    & > span {
      font-size: 12px;
      line-height: 15px;
      letter-spacing: 0.86px;
      opacity: 0.4;
      margin: 0 5px;
    }
  }
}

.th {
  text-align: left;
  white-space: nowrap;

  &:nth-child(2) {
    width: 10000px;
  }
}

.td {
  text-align: left;
  white-space: nowrap;
}

.money {
  white-space: nowrap;
}
